<template>
    <div class="new-message">
        <form @submit.prevent="addMessage">
            <input placeholder="Send a message" type="text" name="new-message" v-model="newMessage" autocomplete="off">            
            <button class="btn teal" @submit.prevent="sendMessage">send</button>
            <p class="red-text" v-if="feedback">{{ feedback }}</p>
        </form>
    </div>
</template>

<script>
import db from '@/firebase/init'

export default {
    name: 'NewMessage',
    props: ['name'],
    data(){
        return{
            newMessage: null,
            feedback: null
        }
    },
    methods: {
        addMessage(){
            if(this.newMessage){
                db.collection('messages').add({
                    content: this.newMessage,
                    name: this.name,
                    timestamp: Date.now()
                }).catch(err => {
                    console.log(err)
                })
                this.newMessage = null
                this.feedback = null
            } else {
                this.feedback = "You must enter a message in order to send one"
            }
        },
        sendMessage(){
            this.addMessage()
        }
    }
}
</script>

<style>
.new-message form{
    display:flex;
}

.new-message button{
    margin:auto;
}
</style>


