<template>
    <div class="chat container">
        <h2 class="center teal-text">Chat Room</h2>
        <div class="card">
            <div class="card-content">
                <ul class="messages" id="messages" v-chat-scroll>
                    <li v-for="message in messages" :key="message.id">
                        
                        <span class="teal-text">{{ message.name }}</span><br/>
                        
                        <span class="grey-text text-darken-3" id="messagebox">{{ message.content }}</span>
                       
                        <span class="grey-text time">{{ message.timestamp }}</span>
                        
                    </li>
                </ul>
            </div>
            <div class="card-action">
                <NewMessage :name="name" />
            </div>
        </div>
    </div>
</template>

<script>
import NewMessage from '@/components/NewMessage'
import db from '@/firebase/init'
import moment from 'moment'

export default {
    name: 'Chat',
    props: ['name'],
    components: {
        NewMessage
    },
    data(){
        return {
            messages: []
        }
    },
    created(){
        let ref = db.collection('messages').orderBy('timestamp')

        ref.onSnapshot(snapshot => {
            snapshot.docChanges().forEach(change => {
                if(change.type == 'added'){
                    let doc = change.doc
                    this.messages.push({
                        id: doc.id,
                        name: doc.data().name,
                        content: doc.data().content,
                        timestamp: moment(doc.data().timestamp).format('LT')
                    })
                }
            })
        })
    },
    updated(){
        var el = document.getElementById('messages'); 
        el.scrollTop = el.scrollHeight;
    }
}
</script>

<style>
.card-content{
    background: #9bbbd4;
}
.chat li{
    padding-top:10px;
}
.chat h2{
    font-size: 2.6em;
    margin-bottom: 40px;
}
.chat span{
    font-size: 1.4em;
}
.chat .time{
    font-size: 0.8em;
}
.messages{
    max-height: 300px;
    overflow: auto;
}
.messages::-webkit-srollbar{
    width: 3px;
}
.messages::-webkit-srollbar-track{
    background: #ddd;
}
.messages::-webkit-srollbar-thumb{
    background: #aaa;
}
</style>



